// 以1. | a. | A. 开始，紧跟字段是中文或英文
export const STATS_WITH_1 = /^1\.[\u4e00-\u9fa5|a-zA-Z]+/;
export const STATS_WITH_A = /^A\.[\u4e00-\u9fa5|a-zA-Z]+/;
export const STATS_WITH_a = /^a\.[\u4e00-\u9fa5|a-zA-Z]+/;

export const LOWER_LETTER_REG = /^[a-z]+[.|。]$/;
export const UPPER_LETTER_REG = /^[A-Z]+[.|。]$/;
export const ARABIC_REG = /^\d+[.|。｜、]$/;

export const NUMBER_POINT = /^\w+[.|。]$/;
export const NUMBER_DON = /^\d+、$/;
export const EN_NUM_BRACKET = /^\(\d+\)$/;
export const ZH_NUM_BRACKET = /^（\d+）$/;
export const EN_NUM_HALF_BRACKET = /^\d+\)$/;
export const ZH_NUM_HALF_BRACKET = /^\d+）$/;
export const EN_LET_HALF_BRACKET = /^[a-z]+\)$/;
export const ZH_LET_HALF_BRACKET = /^[a-z]+）$/;

export const STICKER_CODE_REG = /^\[.*\]$/;
